body {
  background-color: #ffdee9;
  background-image: linear-gradient(0deg, #ffdee9 0%, #b5fffc 100%);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

img {
  max-width: 100%;
  display: block;
}

.photo-card {
  max-width: calc(25% - 11.25px);
  transition: transform .7s ease-in-out;
  box-shadow: 0 48px 100px rgba(17, 12, 46, .15);
}

.photo-card:hover {
  transform: scale(1.05);
}

.gallery {
  max-width: 100%;
  flex-wrap: wrap;
  gap: .9375rem;
  display: flex;
}

.info-item {
  flex-direction: column-reverse;
  align-items: center;
  column-gap: .9375rem;
  font-size: .75rem;
  display: flex;
}

.info {
  max-width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: .3125rem;
  display: flex;
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.end-result {
  color: gray;
  margin: 30px auto 60px;
  font-family: Cambria, Cochin, Georgia, Times, Times New Roman, serif;
  font-size: 26px;
}

.show-more {
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  height: 54px;
  letter-spacing: .4px;
  max-width: 100%;
  text-transform: uppercase;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background-image: linear-gradient(135deg, #f34079 40%, #fc894d);
  border: 0;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 15px auto;
  padding-top: 3px;
  padding-left: 20px;
  padding-right: 20px;
  font-family: Codec cold, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
  display: flex;
}

.show-more:active, .show-more:hover {
  outline: 0;
}

.show-more span {
  transition: all .2s;
}

.show-more:hover span {
  opacity: .75;
  transform: scale(.9);
}

@media screen and (max-width: 991px) {
  .show-more {
    height: 50px;
    font-size: 15px;
  }

  .show-more span {
    line-height: 50px;
  }
}

input {
  background: rgba(#d4bdbd, green, blue, alpha);
  height: 40px;
  z-index: 4;
  border: none;
  border-radius: 9999em;
  outline: none;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 5px;
  font-size: 1.2em;
  display: inline-block;
  position: relative;
}

.search-icon {
  min-width: 30px;
  min-height: 30px;
  transition: transform .5s ease-in-out;
}

.search-button {
  box-sizing: border-box;
  min-width: 50px;
  height: 50px;
  white-space: nowrap;
  cursor: pointer;
  z-index: 5;
  border: 0;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  font-family: Phantomsans, sans-serif;
  font-size: 20px;
  line-height: 1em;
  display: flex;
  position: relative;
  right: 40px;
  box-shadow: 0 15px 30px -5px rgba(151, 65, 252, .2);
}

.search-button:hover .search-icon {
  transform: scale(1.1);
}

.is-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

